<template>
	<div class="dropdown" :class="{'active': active}" >
		<button class="btn dropdown-toggle" @click.stop="toggleMenu">
			<slot name="title"></slot>
		</button>
		<div class="icon-wrap">
			<ArrowIconDown/>
		</div>
			<PerfectScrollbar class="dropdown-menu" tag="ul">
				<slot name="list"></slot>
			</PerfectScrollbar>
	</div>
</template>
<script>
	import ArrowIconDown from "../Icons/ArrowIconDown"

	export default {
		name: 'DropDown',
		props: {
			title: {
				default: 'Title',
				type: String
			}
		},
		components: {ArrowIconDown},
		data() {
			return {
				active: false
			}
		},
		methods: {
			toggleMenu() {
				this.active = !this.active
			}
		}
	}
</script>
