<template>
	<table class="table table-main">
		<thead>
		<tr>
			<th scope="col" v-if="visibleCountsLength > 0">
				<div><CheckBox @input="toggleSelectAllOnPage" :item-active="selectAllOnPage"/></div>
			</th>
			<th v-if="visibleCounts.product" scope="col">
				<div>
					Product (100g serving)
					<ArrowSort :class="{'item-toggle': visibleCountsArrowIcon.toggle} " v-show="visibleCountsArrowIcon.show === 'product'"/>
				</div>
			</th>
			<th v-if="visibleCounts.calories" scope="col">
				<div>
					Calories
					<ArrowSort :class="{'item-toggle': visibleCountsArrowIcon.toggle} " v-show="visibleCountsArrowIcon.show === 'calories'"/>
				</div>
			</th>
			<th v-if="visibleCounts.fat" scope="col">
				<div>
					Fat (g)
					<ArrowSort :class="{'item-toggle': visibleCountsArrowIcon.toggle} " v-show="visibleCountsArrowIcon.show === 'fat'"/>
				</div>
			</th>
			<th v-if="visibleCounts.carbs" scope="col">
				<div>
					Carbs (g)
					<ArrowSort :class="{'item-toggle': visibleCountsArrowIcon.toggle} " v-show="visibleCountsArrowIcon.show === 'carbs'"/>
				</div>
			</th>
			<th v-if="visibleCounts.protein" scope="col">
				<div>
					Protein (g)
					<ArrowSort :class="{'item-toggle': visibleCountsArrowIcon.toggle} " v-show="visibleCountsArrowIcon.show === 'protein'"/>
				</div>
			</th>
			<th v-if="visibleCounts.iron" scope="col">
				<div>
					Iron (%)
					<ArrowSort :class="{'item-toggle': visibleCountsArrowIcon.toggle} " v-show="visibleCountsArrowIcon.show === 'iron'"/>
				</div>
			</th>
			<th scope="col"></th>
		</tr>
		</thead>
		<tbody>
		<tr v-for="item in showProducts" :key="item.id">
			<th scope="row">
				<div><CheckBox @input="toggleChecked(item)" v-model="item.checked" :item-active="item.checked"/></div>
			</th>
			<td v-if="visibleCounts.product" >
				<div>{{item.product}}</div>
			</td>
			<td v-if="visibleCounts.calories">
				<div>{{item.calories}}</div>
			</td>
			<td v-if="visibleCounts.fat">
				<div>{{item.fat}}</div>
			</td>
			<td v-if="visibleCounts.carbs">
				<div>{{item.carbs}}</div>
			</td>
			<td v-if="visibleCounts.protein">
				<div>{{item.protein}}</div>
			</td>
			<td v-if="visibleCounts.iron">
				<div>{{item.iron}}</div>
			</td>
			<td class="delete-td" @click="deleteItem($event, item.id)">
				<div class="d-flex align-items-center w-100 h-100">
					<DeleteIcon/>
					<span>delete</span>
				</div>
			</td>
		</tr>
		</tbody>
	</table>
</template>
<script>
	import {mapState} from "vuex";
	import DeleteIcon from "../UI/Icons/DeleteIcon";
	import CheckBox from "../UI/CheckBox";
	import {tableConfig} from "../../plugins/deletePopup";
	import {EventBus} from "../../bus/eventBus";
	import ArrowSort from "../UI/Icons/ArrowSort";

	export default {
		name: 'TableBody',
		components: {ArrowSort, CheckBox, DeleteIcon},
		data() {
			return {
				showProducts: [],
				visibleCounts: {},
				visibleCountsArrowIcon: {
					show: false,
					toggle: false
				},
				visibleCountsLength: null,
				selectAllOnPage: false,
				baseControls: {
					visibleItemsCount: 5,
					minVisibleIndex: 0,
				}
			}
		},
		methods: {
			toggleChecked(item) {
				this.$store.commit('toggleChecked', item)
			},
			toggleSelectAllOnPage() {
				this.selectAllOnPage = !this.selectAllOnPage
				const minMax = {
					min: this.baseControls.minVisibleIndex,
					max: this.baseControls.visibleItemsCount
				}
				this.showProducts = this.showProducts.map(item => ({...item, checked: this.selectAllOnPage}))
				const targets = this.showProducts
				this.$store.commit('toggleSelectAllOnPage', {minMax, targets})
			},
			getCurrentProducts() {
				this.showProducts = this.products.slice(this.baseControls.minVisibleIndex, this.baseControls.visibleItemsCount)
				this.selectAllOnPage = this.showProducts.every(item => item.checked === true)
			},
			deleteItem(e, id) {
				const target = e.target.tagName === 'TD' ? e.target : e.target.closest('td')
				const {width, left, height, y} = target.getBoundingClientRect()
				this.$table(tableConfig.delete, {top: Math.floor(y + height), left: Math.floor(left - width), id})
			},
		},
		computed: {
			...mapState(['products'])
		},
		watch: {
			products() {
				this.getCurrentProducts()
			}
		},
		created() {
			EventBus.$on(tableConfig.changeVisibleCount, ({min, current}) => {
				this.baseControls.minVisibleIndex = min
				this.baseControls.visibleItemsCount = min +current
				this.getCurrentProducts()
			})
			EventBus.$on(tableConfig.changePagination, ({min, current}) => {
				this.baseControls.minVisibleIndex = min
				this.baseControls.visibleItemsCount = min +current
				this.getCurrentProducts()
			})
			EventBus.$on(tableConfig.changeVisibleColumns, (params) => {
				const activeColumns = {}
				let count = 0
				params.forEach(item => {
					if (item.show) {
						count += 1
						activeColumns[item.product] = item.show
					}
					if (item.show && item.sort.target) {
						this.visibleCountsArrowIcon = {
							show: item.product,
							toggle: item.sort.sortDirection
						}
					}
				})
				this.visibleCounts = activeColumns
				this.visibleCountsLength = count
			})
		},
		beforeDestroy() {
			EventBus.$off(tableConfig.changeVisibleCount)
			EventBus.$off(tableConfig.changePagination)
			EventBus.$off(tableConfig.changeVisibleColumns)
		}

	}
</script>

<style lang="scss">
	.table-main {
		& thead {
			& .arrow-sort {
				margin-left: 8px;
			}
		}
		& .delete-td {
			opacity: 0;
			transition: .2s ease;
			cursor: pointer;

			& span {
				margin-left: 4px;
				font-size: 14px;
				line-height: 24px;
			}
		}

		& tr {
			&:hover {
				.delete-td {
					opacity: 1;
				}
			}
		}
	}

</style>
