import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		products: []
	},
	getters: {
		selectedForDelete: state => state.products.filter(item => item.checked === true).length
	},
	mutations: {
		getProducts(state, payload) {
			state.products = payload.map(item => ({...item, checked: false}))
		},
		deleteItem(state, payload) {
			const oldProducts = state.products
			state.products = oldProducts.filter(product => product.id !== payload)
		},
		deleteSelectItems(state) {
			state.products = state.products.filter(item => item.checked === false)
		},
		toggleChecked(state, payload){
			const oldState = state.products
			const target = oldState.findIndex(item => item.id === payload.id)
			oldState[target] = payload
			state.products = oldState
		},
		toggleSelectAllOnPage(state, {minMax, targets}) {
			const oldState = state.products
			const before = oldState.slice(0, minMax.min)
			const after = oldState.slice(minMax.max, oldState.length)
			state.products =  [...before, ...targets, ...after]
		},
		sortProducts(state, {category, direction}) {
			state.products = state.products.sort((a, b) => {
				if (direction) {
					return a[category] > b[category] ? 1 : -1
				} else {
					return a[category] < b[category] ? 1 : -1
				}
			})
		},
	},
	actions: {},
})
