<template>
	<label :for="id" class="custom-checkbox">
		<input type="checkbox" :id="id" @input="sendValue" :checked="itemActive">
		<div class="checkbox-icon">
			<CheckIcon />
		</div>
		<span v-if="description">{{description}}</span>
	</label>
</template>

<script>
	import CheckIcon from "./Icons/CheckIcon";

	export default {
		name: "CheckBox",
		props: [
			'description',
			'id',
			'itemActive'
		],
		components: {
			CheckIcon
		},
		methods: {
			sendValue(e) {
				this.$emit('input', e.target.checked)
			}
		}
	}
</script>

