<template>
	<div class="table-navigation mb-1 align-items-center">
		<p class="mr-1"><strong>Sorting by:</strong></p>
		<ul class="d-flex align-items-center tags-navigation flex-grow-1">
			<li v-for="(item, index) in tags" :key="index" @click="findActiveTag(index)">
				<Tags :class="{'active': item.active}" v-if="item.show">
					{{item.title}}
				</Tags>
			</li>
		</ul>
		<div class="btn-wrap ml-1">
			<button :disabled="selectedForDelete === 0" :class="{'active': selectedForDelete > 0}"
			        class="btn btn-outline-primary" @click="deleteSelectItems">
				Delete
				<span style="margin-left: 4px" v-if="selectedForDelete > 0">({{selectedForDelete}})</span>
			</button>
			<DropDown>
				<template v-slot:title>{{perPage.current}} Per Page</template>
				<template v-slot:list>
					<drop-down-list v-for="(item) in perPage.perPageSize" :key="item.size">
						<span @click.stop="setPerPage(item)">{{item.size}} Per Page</span>
					</drop-down-list>
				</template>
			</DropDown>
			<div class="btn-wrap">
				<button class="btn btn-outline-primary" @click="setPagination('left')"
				        :disabled="pagination.min === 0">
					<ArrowIconLeft/>
				</button>
				<div class="count-info">
					{{pagination.min + 1}} - {{pagination.max}} of {{products.length}}
				</div>
				<button class="btn btn-outline-primary" @click="setPagination('right')"
				        :disabled="pagination.max === products.length">
					<ArrowIconRight/>
				</button>
			</div>
			<DropDown>
				<template v-slot:title>{{tags.length}} columns selected</template>
				<template v-slot:list>
					<drop-down-list>
						<CheckBox id="item-0" description="Select All" class="fw-600" :item-active="isSelectAll"
						          @input="toggleAllActive"/>
					</drop-down-list>

					<drop-down-list v-for="(item) in tags" :key="item.title">
						<CheckBox :description="item.title" v-model="item.show" :item-active="item.show"
						          @input="checkIsAllActive"/>
					</drop-down-list>
				</template>
			</DropDown>
		</div>
	</div>
</template>

<script>
	import DropDown from "../UI/DropDown/DropDown";
	import DropDownList from "../UI/DropDown/DropDownList";
	import ArrowIconLeft from "../UI/Icons/ArrowIconLeft";
	import ArrowIconRight from "../UI/Icons/ArrowIconRight";
	import {mapGetters, mapState} from "vuex";
	import Tags from "../UI/Tags";
	import CheckBox from "../UI/CheckBox";
	import {tableConfig} from "../../plugins/deletePopup";

	export default {
		name: "TableNavigation",
		components: {CheckBox, Tags, ArrowIconRight, ArrowIconLeft, DropDownList, DropDown},
		computed: {
			...mapState(['products']),
			...mapGetters(['selectedForDelete'])
		},
		data() {
			return {
				isSelectAll: false,
				perPage: {
					perPageSize: [
						{active: false, size: 5,},
						{active: true, size: 10,},
						{active: false, size: 15,},
						{active: false, size: 200,},
					],
					current: null
				},
				tags: [
					{title: 'Product (100g serving)', product: 'product',},
					{title: 'Calories', product: 'calories',},
					{title: 'Fat (g)', product: 'fat',},
					{title: 'Carbs (g)', product: 'carbs',},
					{title: 'Protein (g)', product: 'protein',},
					{title: 'Iron (%)', product: 'iron',},
				],
				pagination: {
					min: 0,
					max: 0,
				},
				trigger: true
			}
		},
		watch: {
			'products.length'() {
				const maxSum = this.pagination.min + this.perPage.current
				this.pagination.max = maxSum > this.products.length ? this.products.length : maxSum
				if (this.trigger) {
					this.findActiveTag(0)
					this.trigger = false
				}
			},
		},
		methods: {
			deleteSelectItems() {
				this.$store.commit('deleteSelectItems')
				this.setPagination()
			},
			checkIsAllActive() {
				this.isSelectAll = this.tags.every(item => item.show !== false)
				this.$table(tableConfig.changeVisibleColumns, this.tags)
			},
			toggleAllActive() {
				const isAllActive = this.tags.every(item => item.show === true)
				this.tags = this.tags.map(item => ({
					...item,
					show: !isAllActive,
				}))

				this.checkIsAllActive()
			},
			setPagination(val) {
				const productLength = this.products.length
				const current = this.perPage.current
				const difference = this.pagination.max - current
				const updateMax = this.pagination.max + current

				if (val === 'left') {
					this.pagination.max = difference >= current ? difference : current
				}
				if (val === 'right') {
					this.pagination.max = updateMax <= productLength ? updateMax : productLength
				}

				this.pagination.min = this.pagination.max - current
				this.$table(tableConfig.changePagination, {min: this.pagination.min, current})
			},
			setPerPage(item = false) {
				this.perPage.perPageSize = this.perPage.perPageSize.map(pageSize => ({
					...pageSize,
					active: pageSize.size === item.size
				}))

				this.perPage.current = item ? item.size : this.perPage.perPageSize[0].size

				const findMax = this.pagination.min + this.perPage.current

				this.pagination.max = findMax <= this.products.length ? findMax : this.products.length

				this.$table(tableConfig.changeVisibleCount, {min: this.pagination.min, current: this.perPage.current})
			},
			findActiveTag(index) {
				this.tags = this.tags.map((item, ind) => {
					const isTarget = index === ind
					const sortDirection = item.sort.sortDirection
					return {
						...item,
						active: isTarget,
						sort: {
							target: isTarget,
							sortDirection: isTarget ? !sortDirection : false
						}
					}
				})
				const {product, sort} = this.tags.find(item => item.sort.target)
				const productLength = this.products.length
				const current = this.perPage.current
				const maxSum = this.pagination.min + current
				this.pagination.min = 0
				this.pagination.max = maxSum > productLength ? productLength : current
				this.$store.commit('sortProducts', {category: product, direction: sort.sortDirection})
				this.checkIsAllActive()
			},
		},
		mounted() {
			this.tags = this.tags.map((item, index) => ({
				...item,
				show: true,
				active: index === 0,
				sort: {
					target: index === 0,
					sortDirection: false
				}
			}))
			this.setPerPage()
			this.checkIsAllActive()
		},
	}
</script>

<style lang="scss">
	.table-navigation {
		display: flex;
		justify-content: space-between;

		& .tags-navigation {
			display: flex;
			align-items: center;

			&:not(:last-child) {
				margin-right: 1rem;
			}
		}

		& .count-info {
			display: flex;
			align-items: center;
			margin-right: 1rem;
		}
	}
</style>