<template>
	<div class="delete-popup" :class="{'active': visible}" ref="popup">
		<p>Are you sure you want <strong>delete item?</strong></p>
		<div class="btn-wrap">
			<button class="btn btn-outline-primary" @click="closePopup">Cancel</button>
			<button class="btn btn-primary" @click="successHandler">Confirm</button>
		</div>
	</div>
</template>

<script>
	import {EventBus} from "../../bus/eventBus";
	import {tableConfig} from "../../plugins/deletePopup";

	export default {
		name: "DeletePopup",
		data() {
			return {
				visible: false,
				id: null
			}
		},
		methods: {
			togglePopup() {
				this.visible = !this.visible
			},
			successHandler() {
				this.$store.commit('deleteItem', this.id)
				this.togglePopup()
			},
			closePopup() {
				this.togglePopup()
			},
		},
		created() {
			EventBus.$on(tableConfig.delete, params => {
				this.id = params.id
				const popup = this.$refs['popup']
				popup.style.top = params.top - 4 + 'px'
				popup.style.left = params.left + 'px'
				this.togglePopup()
			})
		},
		beforeDestroy() {
			EventBus.$off(tableConfig.delete)
		}
	}
</script>

<style lang="scss">
	@import "src/styles/base-functions";

	.delete-popup {
		display: none;
		width: 260px;
		padding: 16px;
		background: #FFFFFF;
		box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.16);
		border-radius: 4px;
		position: absolute;
		z-index: $z-popup;

		&.active {
			display: block;
		}

		& p {
			margin-bottom: 8px;
		}
	}
</style>