import Vue from 'vue'

import {EventBus} from '../bus/eventBus'

const table = {
	install(Vue) {
		Vue.prototype.$table = function (eventName, eventParams) {
			EventBus.table(eventName, eventParams)
		}
	},
}

Vue.use(table)

export const tableConfig = {
	delete: 'delete',
	changePagination: 'changePagination',
	changeVisibleCount: 'changeVisibleCount',
	changeVisibleColumns: 'changeVisibleColumns',
	deleteSelectItems: 'deleteSelectItems',
}
